<template>
  <!-- 全部作品中的table组件 -->
  <div class="allWorks-table">
    <div class="header">
      <div class="header-tit">参赛组别</div>
      <!-- <b-scroll></b-scroll> -->
      <div class="use-scrollX">
        <div class="true-scroll" :style="'width:'+scrollWidth+'px'">
          <div
            class="scroll-item"
            v-for="(item ,key) in groupRender"
            :key="key"
            :class="{change: item.change}"
            @click="nameClick(key, item.chage)"
          >{{item.name}}</div>
        </div>
      </div>
    </div>
    <div class="content-box">
      <div class="card-item" v-for="(item, key) in list" :key="key" @click="worksDescribe">
        <div class="tag-img-group">
          <span class="tag">170****6042</span>
          <el-image :src="item.img" class="img" fit="cover" />
        </div>
        <div class="info-group">
          <span class="group-name">我是组别组别</span>
          <span class="name">我是姓名</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {
      name: [
        { name: '全部', change: false },
        { name: '少儿组', change: true },
        { name: '1-2年级组', change: false },
        { name: '3年级组', change: false },
        { name: '4-6年级组', change: false }
      ],
      list: [
        {
          img:
            '//qpic.y.qq.com/music_cover/0YKXV0pAP6fzSlwyxGcKKdYYDFZkhQSNM9KeEibFajvgp3tnlvdpAlg/300?n=1'
        },
        {
          img:
            '//qpic.y.qq.com/music_cover/AhbCa0vazSRDjEJhYwthgjxuuCvKe2gibhbfdQrrrcLaOFjFFiceBNpw/300?n=1'
        },
        {
          img:
            '//qpic.y.qq.com/music_cover/MhQ4bJBPt3Yt5icXyBGNhyLhzgDloRibMyDIScvDhLw1aC9thCSjxBoQ/300?n=1'
        },
        {
          img:
            '//y.gtimg.cn/music/photo_new/T002R300x300M0000023F4jz2ZTLNz.jpg?max_age=2592000'
        },
        {
          img:
            '//y.gtimg.cn/music/photo_new/T002R300x300M000003NLdQI3OCde3.jpg?max_age=2592000'
        }
      ]
    }
  },
  props: {
    user: {
      type: String,
      default: ''
    }
  },
  computed: {
    scrollWidth () {
      const sum = this.name.length
      return sum * (90 + 20)
    },
    groupRender () {
      if (this.user === 'gameWorks') {
        return this.name
      } else {
        // eslint-disable-next-line vue/no-side-effects-in-computed-properties
        this.name.splice(0, 1)
        return this.name
      }
    }
  },
  methods: {
    // 跳转作品详情
    worksDescribe () {
      this.$router.push({ path: '/ds/detail' })
    },
    nameClick (i, change) {
      if (change) return
      this.name.map(item => {
        item.change = false
      })
      this.name[i].change = true
    }
  },
  components: {
    BScroll
  }
}
</script>
<style lang="less">
.allWorks-table {
  width: 100%;
  .header {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding-bottom: 15px;
    .header-tit {
      font-size: 14px;
      color: #999999;
      margin-right: 16px;
    }
    .use-scrollX {
      overflow: auto;
      width: 80%;
      flex: 1;
      .true-scroll {
        // width: 400%;
        display: flex;
        align-items: center;
        justify-content: flex-start;
      }
      .scroll-item {
        width: 90px;
        padding: 4px 0;
        font-size: 16px;
        color: #999999;
        border-radius: 6px;
        background: #e5e5e5;
        margin-right: 20px;
        text-align: center;
      }
      .change {
        color: #ffffff;
        background: #fe741d;
      }
    }
  }
  .content-box {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .card-item {
      width: 48%;
      // height: 7vh;
      height: 120px;
      box-shadow: 0px -1px 5px 0px rgba(196, 196, 196, 0.2),
        0px 2px 8px 0px rgba(226, 226, 226, 0.5);
      border-radius: 4px;
      overflow: hidden;
      margin: 5px 0;
      .tag-img-group {
        position: relative;
        width: 100%;
        // height: 11vh;
        height: 90px;
        .tag {
          position: absolute;
          top: 0;
          left: 0;
          border-radius: 0px 11px 11px 0px;
          background-color: #7f7f7f;
          opacity: 0.7;
          color: #ffffff;
          font-size: 9px;
          padding: 5px;
          z-index: 1;
        }
        .img {
          width: 100%;
          height: 100%;
        }
      }
      .info-group {
        width: 100%;
        box-sizing: border-box;
        padding: 6px 10px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .group-name,
        .name {
          font-size: 12px;
          color: #333333;
        }
      }
    }
  }
}
</style>



