<template>
  <!-- 大赛作品 -->
  <div class="game-works">
    <div class="control-search">
      <search-comp :showSearch="false" :arr="arr"></search-comp>
    </div>
    <div class="text-tit">
      <div class="tit">
        <title-comp tit='大赛作品'></title-comp>
      </div>
      <div class="more-btn" @click="toAllWorks">查看全部 》</div>
    </div>
    <div class="works-table">
      <div class="use-padding">
        <allTable user='gameWorks'></allTable>
      </div>
      <div class="works-more-btn">
        <span @click="toAllWorks">更多</span>
      </div>
    </div>
    <!-- 以下是晋级名单 -->
    <div class="text-tit">
      <div class="tit">
        <title-comp tit='晋级名单'></title-comp>
      </div>
      <div class="more-btn" @click="toNameAll">查看全部 》</div>
    </div>
    <div class="name-table">
      <div class="use-padding">
        <name-table>
          <div class="name-more-btn">
            <span @click="toNameAll">更多</span>
          </div>
        </name-table>
      </div>
      <div class="use-padding">
        <name-table>
          <div class="name-more-btn">
            <span @click="toNameAll">更多</span>
          </div>
        </name-table>
      </div>
    </div>
  </div>
</template>
<script>
import titleComp from '../../../components/titleComp/titleComp'
import searchComp from '../../../components/searchFlowPath/searchFlowPath'
import allTable from '../../../components/allWorksTable/allWorksTable'
import nameTable from '../../../components/table/table'
export default {
  data () {
    return {
      arr: [
        { class: 'step-start', text: '报名及地区初选' },
        { class: 'step', text: '地区复选' },
        { class: 'step', text: '地区决选' },
        { class: 'step-end', text: '全国决选' }
      ]
    }
  },
  components: {
    searchComp,
    allTable,
    nameTable,
    titleComp
  },
  methods: {
    toAllWorks () {
      this.$router.push({ path: '/option/allWorks', query: {} })
    },
    toNameAll () {
      this.$router.push({ path: '/option/nameList', query: {} })
    }
  }
}
</script>
<style lang="less">
.game-works {
  width: 100%;
  box-sizing: border-box;
  padding: 0 18px;
  // margin-bottom: 20px;
  margin-bottom: 100px;
  .control-search {
    // margin-top: 15px;
    padding-top: 15px;
  }
  .text-tit {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .tit {
      // font-size: 16px;
      // color: #333333;
      // font-weight: bold;
      // margin-top: 12px;
      padding-top: 12px;
      // letter-spacing: 1px;
      width: 150px;
    }
    .more-btn {
      font-size: 12px;
      color: #333333;
      letter-spacing: 1px;
      padding: 10px 5px;
    }
  }
  .works-table {
    width: 100%;
    // margin-top: 12px;
    padding-top: 12px;
    .use-padding {
      // margin-bottom: 10px;
      padding-bottom: 10px;
    }
    .works-more-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      span {
        padding: 4px 23px;
        font-size: 12px;
        color: #fe741d;
        border-radius: 12px;
        border: 1px solid #fe741d;
      }
    }
  }
  .name-table {
    padding-top: 15px;
    .use-padding{
      margin: 10px 0;
    }
    .name-more-btn {
      padding-top: 15px;
      display: flex;
      align-items: center;
      justify-content: center;
      span {
        padding: 4px 23px;
        font-size: 12px;
        color: #fe741d;
        border-radius: 12px;
        border: 1px solid #fe741d;
      }
    }
  }
}
</style>



