<template>
  <!-- 列表数据组件 -->
  <div class="table-comp">
    <div class="title-group">
      <div class="tit">学前少儿组晋级名单</div>
      <div class="total">共12名</div>
    </div>
    <div class="list-box">
      <table class="table">
        <thead class="thead">
          <tr>
            <th>序号</th>
            <th>姓名</th>
            <th>学校</th>
            <th>联系方式</th>
            <th>评分</th>
          </tr>
        </thead>
        <tbody class="tbody">
          <tr>
            <td>01</td>
            <td>我是姓名</td>
            <td>人民小学</td>
            <td>176****9096</td>
            <td>8.0</td>
          </tr>
          <tr>
            <td>01</td>
            <td>我是姓名</td>
            <td>人民小学</td>
            <td>176****9096</td>
            <td>8.0</td>
          </tr>
        </tbody>
      </table>
    </div>
    <slot></slot>
  </div>
</template>
<script>
export default {
  props: {
    obj: {
      type: Object,
      default () {
        return {}
      }
    }
  }
}
</script>
<style lang="less">
.table-comp {
  width: 100%;
  box-sizing: border-box;
  padding: 14px 10px;
  border-radius: 5px;
  box-shadow: 0px -1px 5px 0px rgba(196, 196, 196, 0.2),
    0px 2px 8px 0px rgba(226, 226, 226, 0.5);
  .title-group {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    box-sizing: border-box;
    padding: 0 5px 15px 0;
    border-bottom: 1px solid #ebebeb;
    .tit {
      font-size: 12px;
      color: #fe741d;
    }
    .total {
      font-size: 12px;
      color: #333333;
    }
  }
  .list-box {
    width: 100%;
    margin-top: 15px;
    .table {
      width: 100%;
      text-align: center;
      border-spacing: 0px 10px;
      .thead {
        font-size: 14px;
        color: #999999;
        margin: 5px 0;
      }
      .tbody {
        font-size: 12px;
        color: #333333;
      }
    }
  }
}
</style>



